.it-wallet-button {
    display: inline-block;
    cursor: pointer;
}

.it-wallet-button-disabled {
    opacity: .5;
    cursor: not-allowed;
}

.it-wallet-button-icon {
    display: block;
    margin-bottom: .5rem;
    padding: .75rem;
    border: 1px solid #000;
    border-radius: 99em;
    font-size: 1.25rem;
    text-align: center;
}

.it-wallet-button-text {
    display: block;
    font-size: .75rem;
    color: #000;
    text-align: center;
}