.login-panel {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 100vw;
    height: 100vh;
}

.login-area {
    width: 400px;
}

.it-wallet-login-error-text {
    margin-bottom: 1rem;
}

.it-wallet-input-field {
    width: 100%;
    margin-bottom: 1rem;
    padding: .5rem 1rem;
    border: 1px solid #ddd;
    border-radius: .35rem;
    font-size: 1.25rem;
    box-sizing: border-box;
}

.it-wallet-button-field {
    width: 100%;
    margin-bottom: 1rem;
    background: #484848;
    border: 1px solid #ddd;
    border-radius: .35rem;
    padding: .5rem 1rem;
    font-size: 1.25rem;
    font-weight: bold;
    color: #FFF;
    box-sizing: border-box;
    cursor: pointer;
}