.content > .it-wallet-section:first-child {
    border-top-left-radius: .35rem;
    border-top-right-radius: .35rem;
    border-bottom: none;
}

.content > .it-wallet-section:last-child {
    border-top: none;
    border-bottom-left-radius: .35rem;
    border-bottom-right-radius: .35rem;
}

.it-wallet-account-name {
    display: block;
    margin-bottom: .25rem;
    font-size: 1rem;
    font-weight: bold;
    text-align: center;
}

.it-wallet-account-public-address {
    display: block;
    font-size: .75rem;
    text-align: center;
}

.it-wallet-copyright-text {
    display: block;
    color: #000;
    font-size: .75rem;
    text-align: center;
}