.it-wallet-header {
    display: flex;
    align-items: center;
    padding: 1rem 0;
}

.it-wallet-title {
    flex: 1;
    -ms-flex: 1;
    align-items: center;
    font-size: 1.5rem;
    font-weight: bold;
    color: #565656;
}

.it-wallet-settings {
    flex: 1;
    -ms-flex: 1;
    text-align: right;
}

.it-wallet-network-panel {
    display: inline-block;
    margin-right: 1rem;
    padding: .5rem 1rem;
    border: 1px solid #565656;
    border-radius: 99em;
    color: #565656;
}

.it-wallet-logout-button {
    font-size: 1.25rem;
    color: #565656;
    cursor: pointer;
}